* {
  margin: 0px;
  text-align: center;
  color: #ffffff;
  font-size: 5vh;
  margin: 0;
  padding: 0%;
}
#Ellipse1 {
  animation: jump 0.6s 0.1s linear infinite;
}
#Ellipse2 {
  animation: jump 0.6s 0.2s linear infinite;
}
#Ellipse3 {
  animation: jump 0.6s 0.3s linear infinite;
}
@keyframes jump {
  50% {
    transform: translate(0, 15px);
  }
}
h1,
div {
  font-family: "VT323", monospace;
}
html,
body {
  height: 100%;
  padding: 0%;
}
p {
  display: block;
}
.ima {
  height: 200px;
  width: 350px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  left: 0;
  right: 0;
  z-index: 0;
}
#body {
  width: 100%;
  min-height: 100vh;
  top: 0px;
  left: 0px;
  z-index: 1000;
  position: relative;
  overflow: hidden;
  padding-bottom: 5vh;

  margin: 0px;
  background-image: url(blurry-gradient-haikei.svg);
}
div.ima {
  border: 3px solid #4903fc;
  border-radius: 10px;
  min-width: 350px;
  min-height: 200px;
  position: relative;
  left: auto;
  right: auto;
}
.subtitle {
  position: relative;
  margin: auto;
  text-align: center;
  color: #ffffff;
  text-shadow: #030002;
  z-index: 1;
}
.backgroundimg {
  opacity: 0.5;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
}
a {
  font-size: 30px;
  text-decoration: none;
  padding: 0%;
  justify-content: left;
  display: block;
}
a:visited {
  color: #00a56e;
}
footer {
  position: relative;
  display: flex;
  bottom: 0px;
  width: 100%;
  background: #0345fc;
  align-items: center;
  justify-content: center;
  background-image: url(layered-steps-haikei.svg);
  background-repeat: no-repeat;
  aspect-ratio: 900/300;
  background-size: cover;
  background-position: center;
}
.layered {
  background-image: url(layered-waves-haike.svg);
  width: 100%;
  aspect-ratio: 900/300;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

button {
  padding: 10px;
  background-color: #9c27b0;
  border-radius: 8px;
  box-shadow: 1px 1px 2px 2px #2c2b2b;
}
#image {
  position: absolute;
  top: 5vh;
  background-image: url(Thumbnail.png);
  background-size: 20vh 30vh;
  min-height: 30vh;
  min-width: 20vh;
}
image div,
button {
  margin-top: 5vh;
  position: relative;
  margin-bottom: 1vh;
  display: block;
  top: 5vh;
}

#image button {
  width: auto;
  height: auto;
  position: relative;
  top: 5px;
  justify-self: center;
}
#upper {
  margin-top: 15vh;
}
svg {
  position: absolute;
  top: 10px;
  left: 0px;
}
header {
  min-height: 20vh;
  height: auto;
  width: 100%;
  background-color: #8200ad;
}
.btn:active {
  transform: translateY(2px);
}
